import { globalState } from "./utils"

interface normalObject {
    [x: string]: any
}

export declare type ObjectType<T> = {
    new (): T
}

export default class Container {
    /**
     *  set 
     */
    public set<T extends normalObject>(setClass: ObjectType<T>, instance: T) {
        if (!globalState.instances.has(setClass)) {
            globalState.instances.set(setClass, instance)
        }
    }

    /**
     *  set 
     */
    public get<T>(getClass: ObjectType<T>): T {
        if (!globalState.instances.has(getClass)) {
            throw new Error(`${getClass.name}  set  get `)
        }

        const instance = globalState.instances.get(getClass)

        //  inject  symbol
        if (!instance[globalState.injectSymbol]) {
            return instance
        }

        for (let [propertyKey, injectClass] of instance[globalState.injectSymbol]) {
            // 
            Object.defineProperty(instance, propertyKey, {
                enumerable: true,
                configurable: true,
                get: () => {
                    return globalState.instances.get(injectClass)
                },
                set: (newValue: any) => {
                    instance.propertyKey = newValue
                }
            })
        }

        return instance
    }
}